.countdown {
    background: linear-gradient(#368af4, #3181f2);
    color: #ffffff;
    font-size: 1.6rem;
    padding: 25px;
}

.countdown strong i {
    margin-right: 20px;
}

.countdown__icons {
    cursor: pointer;
    display: none;
    float: right;
    font-size: 1.2rem;
}

.countdown:hover .countdown__icons {
    display: block;
} 

.countdown:hover .overlay span {
    display: block;
}