html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root,
.app {
    height: 100%;
    background: #D7D2BF;
    /* background-image: url('../images/diary.jpg'); */
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: 50% 50%;
    background-size: auto;
    overflow-x: hidden;
}

.app {
    background: #3181f2;
    max-width: 500px;
    margin: 0 auto;
}


.overlay {
    float: right;
}

.overlay span {
    float: right;
    font-size: 1.5rem;
    cursor: pointer;
    display: none;
    padding: 0 10px;
}

.overlay__modal {
    background-image: -webkit-linear-gradient(330deg, #fdfbfb 0%, #ebedee 100%);
    background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
    color: #000;
    display: none;
    height: 100%;
    left: 0;
    padding: 50px;
    position: fixed;
    top: 0;
    width: 100%;
}

.overlay__modal--visible {
    display: block;
}
.countdown {
    background: -webkit-linear-gradient(#368af4, #3181f2);
    background: linear-gradient(#368af4, #3181f2);
    color: #ffffff;
    font-size: 1.6rem;
    padding: 25px;
}

.countdown strong i {
    margin-right: 20px;
}

.countdown__icons {
    cursor: pointer;
    display: none;
    float: right;
    font-size: 1.2rem;
}

.countdown:hover .countdown__icons {
    display: block;
} 

.countdown:hover .overlay span {
    display: block;
}
.edit-event {
    background: #bb1964;
    padding: 20px;
  }
  
  .edit-event__input-group {
    color: #fff;
    font-size: 1.4rem;
    margin-bottom: 10px;
  }
  
  .edit-event__input-group label {
    display: inline-block;
    width: 25%;
  }
  
  .edit-event__input-group input {
    border: 0;
    padding: 5px;
    width: 70%;
  }
  
  .edit-event button {
    background: transparent;
    border: solid 3px #fff;
    cursor: pointer;
    color: #fff;
    font-size: 1.4rem;
    margin-top: 10px;
    padding: 5px;
    text-transform: uppercase;
    width: 100%;
  }
  
  .edit-event button:disabled {
    opacity: 0.4;
  }
